.App {
    background-color: #598690;
    min-height: 100vh;
    font-size: calc(10px + 2vmin);
    color: white;
    display: flex;
    flex-direction: column;
    padding-top: 60px;
    overflow-x: hidden;
}

.navbar {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: #333;
  padding: 10px 0;
  display: flex;
  justify-content: center;
  gap: 30px;
  z-index: 1000;
}

.nav-link {
  color: white;
  text-decoration: none;
  font-weight: 500;
  font-size: 1.1rem;
  transition: color 0.3s ease;
}

.nav-link:hover {
  color: #ffcc00;
}

.Content {
  padding-left: 150px;
  padding-top: 50px;
}

.image-size{
  max-width: 100%;
  height: 500px;
  display: block;
  border-radius: 50px;

}

.text{
  display: block;
  justify-content: right;
  margin-top: 250px;
}

.Name{
  font-family: Lucida Console;
  margin-left: 15px;
}

.intro{
  font-family: Lucida Console;
  font-size: 20px;
  margin-left: 15px;
  margin-right: 100px;
  line-height: 1.5;
}

.aboutSection {
  margin-left: 100px;
  margin-top: 50px;
  padding: 20px;
  display: flex;
}

.education{
  font-size: 30px;
  display: flex;
  justify-content: left;
  margin-left: 100px;
}

.tools{
  margin-top: 40px;
  font-size: 30px;
  display: flex;
  justify-content: left;
  margin-left: 100px;
}

.projectSection {
  font-size: 30px;
  display: flex;
  justify-content: left;
  margin-left: 100px;
}

.projectHeader{
  font-size: 30px;
}

.workflow{
    width: 400px;
    height: auto;
}

.yummyfood{
    width: 300px;
    height: auto;
}

.musicDB{
  height: 200px;
}

.workflowText{
  font-size: 20px;
}

.title{
  text-decoration: none;
  color: white;
  font-weight: bold;
  font-size: 1.4rem;
  margin: 0 0 0 0;
}

.projectContainer{
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin-left: 100px;
  width: 300px;
  margin-top: 15px;
}
.projectsWrapper {
  display: flex;
  justify-content: left;
  flex-wrap: wrap;
  margin-top: 10px;
  gap: 15px;
}

.educationText{
   margin-left: 100px;
   font-family: Lucida Console;
   font-size: 20px;
}

.logoSection{
  display: flex;
  margin-left: 100px;
  margin-right: 100px;
}

.logos{
  width: 75px;
  height: auto;
}

.logoContainer{
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  margin-left: 100px;
}

.logoLabel{
  margin-top: 8px;
  font-size: 1.2rem;
  color: white;
  font-family: Lucida Console;
}

.contactHeader{
  font-size: 30px;
}

.projectSection {
  font-size: 30px;
  display: flex;
  justify-content: left;
  margin: 50px 100px 50px 100px;
  flex-direction: column;
}

.gitHub a{
  text-decoration: none;
  color: white;
  font-weight: bold;
  font-size: 1.4rem;
}

.gitHub a:hover {
  text-decoration: underline;
}

.contactSection {
  font-size: 20px;
  justify-content: left;
  margin-left: 100px;
  margin-bottom: 100px;
  display: flex;
  flex-direction: column;
}

.linkedIn a{
  text-decoration: none;
  color: white;
  font-weight: bold;
  font-size: 1.4rem;
  margin-bottom: 15px;
}

.linkedIn a:hover {
  text-decoration: underline;
}

.link a{
  text-decoration: none;
  color: white;
  font-weight: bold;
  font-size: 1.4rem;
}

.link a:hover {
  text-decoration: underline;

}

.footer {
  background-color: #333;
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px 0;
  width: 100%;
}


.footerDetails{
  display: flex;
  flex-direction: row;
  gap: 50px;
  align-items: center;
  color: white;
}

.footerDetails a {
  text-decoration: none;
  color: white;
  font-weight: bold;
  font-size: 1.4rem;
}

.footerDetails a:hover {
  text-decoration: underline;
}

@media (max-width: 768px) {
  /* General page adjustments */
  .App {
    font-size: 16px;
    padding-top: 60px;
    display: flex;
    flex-direction: column;
    align-items: center;
    overflow-x: hidden;
  }

  /* ABOUT SECTION */
  .aboutSection {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 40px 20px;
    text-align: center;
  }

  .image-size {
    max-width: 75%;
    height: auto;
    display: block;
    border-radius: 20px;
    margin-right: 100px;
    align-items: center;
  }

  .text {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
    padding: 0 10px;
    margin-right: 75px;
  }

  .Name {
    font-size: 1.5rem;
    font-family: "Lucida Console", monospace;
    margin-bottom: 10px;
  }

  .intro {
    font-size: 1rem;
    font-family: "Lucida Console", monospace;
    line-height: 1.6;
    margin: 0;
  }

  /* EDUCATION + TOOLS */
  .education{
    align-text: center;
    margin-left: 150px;
  }
  .tools,
  .projectSection,
  .contactSection {
    text-align: center;
    margin: 30px 20px 10px;
  }

  .educationText {
    margin: 0 20px;
    font-size: 1rem;
    text-align: center;
  }

  /* LOGOS */
  .logoSection {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    margin-right: 160px;
  }

  .logoContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    width: 80px;
  }

  .logos {
    width: 50px;
    height: auto;
  }

  .logoLabel {
    margin-top: 8px;
    font-size: 0.9rem;
  }

  /* PROJECTS */
  .projectsWrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 30px;
    padding: 0 20px;
  }

  .projectContainer {
    width: 100%;
    max-width: 320px;
    text-align: center;
    align-items: center;
    margin-right: 75px;
  }

  .workflow,
  .yummyfood,
  .musicDB {
    max-width: 100%;
    height: auto;
    margin: 0 auto;
  }

  .workflowText {
    font-size: 1rem;
    margin-top: 10px;
  }

  /* CONTACT SECTION */
  .linkedIn {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 5px;
  }

  .linkedIn a {
    font-size: 1.1rem;
  }

  /* FOOTER */
  .footer {
    width: 100%;
    background-color: #333;
    padding: 20px 0;
    display: flex;
    justify-content: center;
  }

  .footerDetails {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    color: white;
  }

  .footerDetails a {
    font-size: 1rem;
  }

  .mn {
    margin: 0;
    font-size: 0.9rem;
  }

  img {
    display: block;
    margin-left: auto;
    margin-right: auto;
  }

}

